import { useField } from 'formik';
import styles from './FormControls.module.css'

export const TextInput = ({ label, validate, ...props }) => {
  const [field, meta] = useField({ ...props, validate });
  let hasError = meta.touched && meta.error;
  return (
    <>
      <input className={hasError ? styles.inputError : null} {...field} {...props} />
      {hasError ? (
        <div className={styles.error}>{meta.error}</div>
      ) : null}
    </>
  );
};

export const FormError = (props) => (
  <>
    {props.error && <div className={styles.formError}>{props.error}</div>}
  </>
)