import { Formik, Form, Field } from "formik";
import { validateEmail, validatePassword } from "../../utils/validators";
import { TextInput, FormError } from "../common/FormControls/FormControls";

function LoginForm({login}) {
  async function onSubmit(values, { setSubmitting, setStatus }) {
    let { resultCode, messages } = await login(values.email, values.password, values.rememberMe);
    if (resultCode === 1) {
      setStatus(messages.length ? messages[0] : "Some error");
    }
    setSubmitting(false);
  }
  return <div>
    <Formik
      initialValues={{ email: '', password: '', rememberMe: false }}
      onSubmit={onSubmit}
    >
      {({ isSubmitting, status }) => (
        <Form>
          <TextInput validate={validateEmail} type="email" name="email" placeholder="Email" />
          <TextInput validate={validatePassword} type="password" name="password" placeholder="Password" />
          <Field type="checkbox" name="rememberMe" /> remember me
          <FormError error={status} />
          <button type="submit" disabled={isSubmitting}>
            Login
          </button>
        </Form>
      )}
    </Formik>
  </div >
}
export default function LoginPage(props) {
  return <div>
    <h1>Login</h1>
    <LoginForm {...props} />
  </div>
}