import styles from './Users.module.css';
import userPhotoMock from '../../assets/images/photoMock.webp';
import { NavLink } from 'react-router-dom';

export default function User({ userData, follow, unfollow, followingInProgress }) {
  return <div>
    <span>
      <div>
        <NavLink to={`/profile/${userData.id}`}>
          <img className={styles.userPhoto} alt=''
            src={userData.photos.small != null ? userData.photos.small : userPhotoMock} />
        </NavLink>
      </div>
      <div>
        {userData.followed
          ? <button disabled={followingInProgress.some(id => id === userData.id)}
            onClick={() => { unfollow(userData.id) }}>Unfollow</button>
          : <button disabled={followingInProgress.some(id => id === userData.id)}
            onClick={() => { follow(userData.id) }}>Follow</button>
        }
      </div>
    </span>
    <span>
      <div>{userData.name}</div>
      <div>{userData.status}</div>
    </span>
  </div>
}