import React from 'react';
import { connect } from "react-redux";
import { follow, unfollow, setCurrentPageNumber, requestUsers } from "../../redux/usersReducer";
import Users from './Users';
import { compose } from 'redux';
import { selectUsersSuper, selectCurrentPageNumber, selectFollowingInProgress, selectIsFetching, selectPageSize, selectTotalUsersCount, selectUsers } from '../../redux/usersSelectors'

class UsersContainer extends React.Component {
  componentDidMount = () => {
    this.props.requestUsers(this.props.currentPageNumber, this.props.pageSize);
  }
  onPageChange = (pNumber) => {
    this.props.requestUsers(pNumber, this.props.pageSize);
  }

  render() {
    let pagesCount = Math.ceil(this.props.totalUsersCount / this.props.pageSize);
    let pageNumbers = [];
    for (let i = 1; i <= pagesCount; i++) {
      pageNumbers.push(i);
    }
    return <Users
      totalUsersCount={this.props.totalUsersCount}
      pageSize={this.props.pageSize}
      currentPageNumber={this.props.currentPageNumber}
      usersData={this.props.usersData}
      unfollow={this.props.unfollow}
      follow={this.props.follow}
      onPageChange={this.onPageChange}
      followingInProgress={this.props.followingInProgress}
      isFetching={this.props.isFetching}
    />
  }
}

function mapStateToProps(state) {
  return {
    usersData: selectUsers(state),
    pageSize: selectPageSize(state),
    totalUsersCount: selectTotalUsersCount(state),
    currentPageNumber: selectCurrentPageNumber(state),
    isFetching: selectIsFetching(state),
    followingInProgress: selectFollowingInProgress(state),
    selectedUsers: selectUsersSuper(state)
  }
}

export default compose(
  connect(mapStateToProps, { follow, unfollow, setCurrentPageNumber, requestUsers })
)(UsersContainer);