import { profileAPI } from "../api/api";

const ADD_POST = 'profile/ADD-POST';
const DELETE_POST = 'profile/DELETE-POST';
const UPDATE_NEW_POST_TEXT = 'profile/CHANGE-NEW-POST-TEXT';
const SET_USER_PROFILE = 'profile/SET-USER-PROFILE';
const SET_STATUS = 'profile/SET-STATUS';

const initialState = {
  profileData: null,
  postsData: [
    { id: 1, message: "Hi, how are you?", likesCount: 15 },
    { id: 2, message: "It's my first post!", likesCount: 20 },
  ],
  newPostText: "",
  status: ""
};

function _addPost(state) {
  const newPostData = {
    id: (state.postsData.length + 1),
    message: state.newPostText,
    likesCount: 0,
  }
  return {
    ...state,
    postsData: [...state.postsData, newPostData],
    newPostText: '',
  }
}
function _updateNewPostText(state, text) {
  return {
    ...state,
    newPostText: text,
  }
}

export default function profileReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_POST:
      return _addPost(state);
    case DELETE_POST:
      return {
        ...state,
        postsData: state.postsData.filter((p) => p.id !== action.id)
      }
    case UPDATE_NEW_POST_TEXT:
      return _updateNewPostText(state, action.text);
    case SET_USER_PROFILE:
      return {
        ...state,
        profileData: action.profileData
      }
    case SET_STATUS:
      return {
        ...state,
        status: action.status
      }
    default:
      return state;
  }
}

export const addPost = () => ({ type: ADD_POST });
export const deletePost = (id) => ({ type: DELETE_POST, id });
export const setStatus = (status) => ({ type: SET_STATUS, status });
export const setUserProfile = (profileData) => ({
  type: SET_USER_PROFILE, profileData
});
export const updateNewPostText = (text) => ({
  type: UPDATE_NEW_POST_TEXT, text
});

export function getProfileData(userId) {
  return async (dispatch) => {
    let data = await profileAPI.getProfileData(userId);
    dispatch(setUserProfile(data));
  }
}
export function getStatus(userId) {
  return async (dispatch) => {
    let data = await profileAPI.getStatus(userId);
    dispatch(setStatus(data));
  }
}
export function updateStatus(newStatus) {
  return async (dispatch) => {
    let data = await profileAPI.updateStatus(newStatus)
    if (data.resultCode === 0) dispatch(setStatus(newStatus));
  }
}