import { useState } from 'react';
import styles from './Paginator.module.css';

export default function Paginator({ onPageChange, currentPageNumber, pageSize, totalItemsCount, portionSize = 10 }) {
  let pagesCount = Math.ceil(totalItemsCount / pageSize);
  let pageNumbers = [];
  for (let i = 1; i <= pagesCount; i++) {
    pageNumbers.push(i);
  }

  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPageNumber = portionNumber * portionSize;

  return <div className={styles.paginator}>
    {portionNumber > 1 && <button onClick={() => setPortionNumber(portionNumber - 1)}>PREV</button>}
    {pageNumbers
      .filter(pN => pN >= leftPortionPageNumber && pN <= rightPortionPageNumber)
      .map(pN => {
        return <span
          onClick={() => onPageChange(pN)}
          key={pN}
          className={ styles.pageNumber + (pN === currentPageNumber ? " " + styles.selectedPage : "")}
        >
          {pN}
        </span>;
      })}
    {portionNumber < portionCount && <button onClick={() => setPortionNumber(portionNumber + 1)}>NEXT</button>}
  </div>
}