import { createSelector } from "reselect";

export function selectUsers(state) {
  return state.usersPage.usersData;
}
export function selectPageSize(state) {
  return state.usersPage.pageSize;
}
export function selectTotalUsersCount(state) {
  return state.usersPage.totalUsersCount;
}
export function selectCurrentPageNumber(state) {
  return state.usersPage.currentPageNumber;
}
export function selectIsFetching(state) {
  return state.usersPage.isFetching;
}
export function selectFollowingInProgress(state) {
  return state.usersPage.followingInProgress;
}
export const selectUsersSuper = createSelector(selectUsers, (users) => {
  return users.filter(u => true);
});