import { usersAPI } from "../api/api";
import copyArrayWithUpdatingObject from "../utils/objectHelpers";

const SET_USERS = 'users/ADD-USERS';
const FOLLOW = 'users/FOLLOW';
const UNFOLLOW = 'users/UNFOLLOW';
const SET_CURRENT_PAGE_NUMBER = 'users/SET-CURRENT-PAGE-NUMBER';
const SET_TOTAL_USERS_COUNT = 'users/SET-TOTAL-USERS-COUNT';
const TOGGLE_IS_FETCHING = 'users/TOGGLE-IS-FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'users/TOGGLE-IS-FOLLOWING-PROGRESS';

const initialState = {
  usersData: [],
  pageSize: 5,
  totalUsersCount: 0,
  currentPageNumber: 1,
  isFetching: true,
  followingInProgress: [],
  fake: 10
};

export default function usersReducer(state = initialState, action) {
  switch (action.type) {
    case SET_USERS:
      return {
        ...state,
        usersData: [...action.usersData]
      };
    case SET_TOTAL_USERS_COUNT:
      return {
        ...state,
        totalUsersCount: action.totalUsersCount
      };
    case FOLLOW:
      return {
        ...state,
        usersData: copyArrayWithUpdatingObject(state.usersData, action.userId, 'id', { followed: true })
      };
    case UNFOLLOW:
      return {
        ...state,
        usersData: copyArrayWithUpdatingObject(state.usersData, action.userId, 'id', { followed: false })
      };
    case SET_CURRENT_PAGE_NUMBER:
      return {
        ...state,
        currentPageNumber: action.currentPageNumber
      };
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching
      };
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userId]
          : state.followingInProgress.filter(id => id !== action.userId)
      };
    default:
      return state;
  }
}

export const setUsers = (usersData) => ({ type: SET_USERS, usersData });
export const setTotalUsersCount = (totalUsersCount) => ({
  type: SET_TOTAL_USERS_COUNT,
  totalUsersCount
});
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });
export const toggleIsFollowingProgress = (isFetching, userId) => (
  { type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userId }
);
export const followSuccess = (userId) => ({ type: FOLLOW, userId });
export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId });
export const setCurrentPageNumber = (currentPageNumber) => {
  return ({ type: SET_CURRENT_PAGE_NUMBER, currentPageNumber })
};

export function requestUsers(pageNumber, pageSize) {
  return async (dispatch) => {
    dispatch(toggleIsFetching(true));
    dispatch(setCurrentPageNumber(pageNumber));
    let data = await usersAPI.getUsers(pageNumber, pageSize);
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
  }
}

async function followUnfollowFlow(dispatch, userId, APIMethod, successAction) {
  dispatch(toggleIsFollowingProgress(true, userId));
  let data = await APIMethod(userId);
  if (data.resultCode === 0) dispatch(successAction);
  dispatch(toggleIsFollowingProgress(false, userId));
}

export function follow(userId) {
  return async (dispatch) => {
    await followUnfollowFlow(
      dispatch,
      userId,
      usersAPI.followUser.bind(usersAPI),
      followSuccess(userId)
    );
  }
}
export function unfollow(userId) {
  return async (dispatch) => {
    await followUnfollowFlow(
      dispatch,
      userId,
      usersAPI.unfollowUser.bind(usersAPI),
      unfollowSuccess(userId)
    );
  }
}