import server from "./server";

export const usersAPI = {
  async getUsers(pNumber = 1, pageSize = 10) {
    const response = await server.get(`users?page=${pNumber}&count=${pageSize}`);
    return response.data;
  },
  async followUser(id) {
    const response = await server.post(`follow/${id}`);
    return response.data;
  },
  async unfollowUser(id) {
    const response = await server.delete(`follow/${id}`);
    return response.data;
  }
}

export const profileAPI = {
  async getProfileData(id) {
    const response = await server.get(`profile/${id}`);
    return response.data;
  },
  async getStatus(id) {
    const response = await server.get(`/profile/status/${id}`);
    return response.data;
  },
  async updateStatus(newStatus) {
    const response = await server.put(`profile/status`, { status: newStatus });
    return response.data;
  }
}

export const authAPI = {
  async getAuthData() {
    const response = await server.get('auth/me');
    return response.data;
  },
  async login(email, password, rememberMe = false) {
    const response = await server.post('auth/login', { email, password, rememberMe, captcha: true });
    return response.data;
  },
  async logout() {
    const response = await server.delete('auth/login');
    return response.data;
  }
}