const SEND_MESSAGE = 'dialogs/SEND-MESSAGE';
const UPDATE_NEW_MESSAGE_TEXT = 'dialogs/UPDATE-NEW-MESSAGE-TEXT';

const initialState = {
  dialogsData: [
    { id: 1, name: "Sveta" },
    { id: 2, name: "Andrey" },
    { id: 3, name: "Natasha" },
    { id: 4, name: "Viktor" },
    { id: 5, name: "Dima" },
  ],
  messagesData: [
    { id: 1, sent: false, message: "Hi" },
    { id: 2, sent: true, message: "How are you?" },
    { id: 3, sent: false, message: "Yo" },
  ],
  newMessageText: "",
};

function _sendMessage(state) {
  const newMessageData = {
    id: (state.messagesData.length + 1),
    sent: true,
    message: state.newMessageText,
  }
  return {
    ...state,
    messagesData: [...state.messagesData, newMessageData],
    newMessageText: '',
  }
}
function _updateNewMessageText(state, text) {
  return {
    ...state,
    newMessageText: text,
  }
}

export default function dialogsReducer(state = initialState, action) {
  switch (action.type) {
    case SEND_MESSAGE:
      return _sendMessage(state);
    case UPDATE_NEW_MESSAGE_TEXT:
      return _updateNewMessageText(state, action.text);
    default:
      return state;
  }
}

export function updateNewMessageTextActionCreator(text) {
  return { type: UPDATE_NEW_MESSAGE_TEXT, text }
}

export function sendMessageActionCreator() {
  return { type: SEND_MESSAGE }
}