import Paginator from '../common/Paginator/Paginator';
import User from './User';
import Preloader from '../common/Preloader/Preloader';

export default function Users({ usersData, follow, unfollow, followingInProgress,
  onPageChange, currentPageNumber, pageSize, totalUsersCount, isFetching }) {
  return <div>
    <Paginator
      onPageChange={onPageChange}
      currentPageNumber={currentPageNumber}
      pageSize={pageSize}
      totalItemsCount={totalUsersCount}
    />
    {isFetching
      ? <Preloader />
      : usersData.map(u => (
        <User
          key={u.id}
          userData={u}
          follow={follow}
          unfollow={unfollow}
          followingInProgress={followingInProgress}
        />))
    }

  </div>
}